<template>
  <div class="home">
    <el-card class="card" shadow="never">
      <el-row :gutter="40">
        <el-col :span="6">
          <img
            v-if="!uploadAvatar.visible"
            class="avatar"
            alt="Avatar"
            :src="avatarURL"
          />
          <el-upload
            v-else
            class="avatar-uploader"
            :action="$http.defaults.baseURL + '/UploadAvatar'"
            name="avatar"
            with-credentials
            :show-file-list="false"
            :before-upload="beforeAvatarUpload"
            :http-request="onAvatarUpload"
          >
            <i class="el-icon-plus avatar-uploader-icon"></i>
          </el-upload>
        </el-col>
        <el-col :span="18">
          <el-descriptions
            title="用户信息"
            :colon="false"
            direction="vertical"
            size="medium"
            border
          >
            <template slot="extra">
              <el-button
                type="primary"
                size="small"
                @click="updatePasswordForm.dialogVisible = true"
              >
                修改密码
              </el-button>
              <el-button
                :type="uploadAvatar.visible ? 'info' : 'primary'"
                size="small"
                @click="uploadAvatar.visible = !uploadAvatar.visible"
              >
                {{ uploadAvatar.visible ? "取消修改" : "修改头像" }}
              </el-button>
              <el-button type="primary" size="small" @click="uploadImage">
                上传图片
              </el-button>
            </template>
            <el-descriptions-item label="账号">
              {{ $store.state.account }}
            </el-descriptions-item>
            <el-descriptions-item label="邮箱">
              {{ $store.state.email }}
            </el-descriptions-item>
            <el-descriptions-item label="昵称">
              {{ $store.state.nickname }}
            </el-descriptions-item>
          </el-descriptions>
        </el-col>
      </el-row>
    </el-card>

    <el-table :data="uploadedFiles" class="table">
      <el-table-column type="index" label="#" />
      <el-table-column prop="path" label="源文件" align="left">
        <template slot-scope="scope">
          <el-link type="primary" :href="'http://' + scope.row.path">
            {{ filename(scope.row.path) }}
          </el-link>
        </template>
      </el-table-column>
      <el-table-column fixed="right" label="操作" align="right">
        <template slot-scope="scope">
          <el-button
            size="mini"
            type="primary"
            @click="predictImage(scope.row.path)"
          >
            预测
          </el-button>
          <el-popconfirm
            style="margin-left: 10px"
            title="确认删除图片？"
            @confirm="deleteImage(scope.row.path)"
          >
            <el-button slot="reference" size="mini" type="danger">
              删除
            </el-button>
          </el-popconfirm>
        </template>
      </el-table-column>
    </el-table>

    <el-dialog
      center
      title="修改密码"
      :visible.sync="updatePasswordForm.dialogVisible"
      @close="clearUpdatePasswordForm"
      width="400px"
    >
      <el-form
        :model="updatePasswordForm"
        :rules="updatePasswordForm.rules"
        ref="updatePasswordForm"
      >
        <el-form-item prop="oldPassword">
          <template slot="label">
            <strong>旧密码</strong>
          </template>
          <el-input
            v-model="updatePasswordForm.oldPassword"
            placeholder="请输入旧密码"
            type="password"
            prefix-icon="el-icon-lock"
            show-password
            clearable
          />
        </el-form-item>
        <el-form-item prop="newPassword">
          <template slot="label">
            <strong>新密码</strong>
          </template>
          <el-input
            v-model="updatePasswordForm.newPassword"
            placeholder="请输入新密码"
            type="password"
            prefix-icon="el-icon-lock"
            show-password
            clearable
          />
        </el-form-item>
        <el-form-item prop="confirmNewPassword">
          <template slot="label">
            <strong>确认新密码</strong>
          </template>
          <el-input
            v-model="updatePasswordForm.confirmNewPassword"
            placeholder="请确认新密码"
            type="password"
            prefix-icon="el-icon-lock"
            show-password
            clearable
          />
        </el-form-item>
      </el-form>
      <template slot="footer">
        <el-button
          :loading="updatePasswordForm.loading"
          type="primary"
          style="width: 100%"
          @click="updatePassword"
        >
          修改密码
        </el-button>
      </template>
    </el-dialog>
  </div>
</template>

<script>
import axios from "axios";
// @ is an alias to /src

export default {
  data() {
    const requiredRule = (msg) => {
      return {
        required: true,
        message: msg,
        trigger: "blur",
      };
    };
    return {
      avatarURL: "",
      uploadedFiles: [],
      uploadAvatar: {
        visible: false,
      },
      updatePasswordForm: {
        loading: false,
        dialogVisible: false,
        oldPassword: "",
        newPassword: "",
        confirmNewPassword: "",
        rules: {
          oldPassword: [requiredRule("请输入旧密码")],
          newPassword: [
            requiredRule("请输入新密码"),
            {
              validator: (_rule, val, callback) => {
                if (this.updatePasswordForm.oldPassword !== val) callback();
                else callback(new Error("新旧密码不能一样"));
              },
            },
          ],
          confirmNewPassword: [
            requiredRule("请重复新密码"),
            {
              validator: (_rule, val, callback) => {
                if (this.updatePasswordForm.newPassword === val) callback();
                else callback(new Error("两次密码不一致"));
              },
            },
          ],
        },
      },
    };
  },
  methods: {
    /**
     * 从 URL 中解析文件名
     *
     * @param {string} path - URL
     * @return {string | undefined}
     */
    filename(path) {
      return this.$R.compose(this.$R.last, this.$R.split("/"))(path);
    },

    /**
     * @returns {string} avatar URL with random query
     */
    generateAvatarURL() {
      return (
        "http://" +
        this.$store.state.avatarPath +
        "?q=" +
        Math.random().toFixed(32).slice(2, 12)
      );
    },

    /**
     * 手动上传头像
     *
     * @param {object} upload
     * @param {string} upload.action
     * @param {File} upload.file
     * @param {string} upload.filename
     */
    onAvatarUpload(upload) {
      const formData = new FormData();
      formData.append("avatar", upload.file);
      axios
        .post("/UploadAvatar", formData)
        .then((res) => {
          /**
           * @type {{code: number, msg: string}}
           */
          const data = res.data;
          console.log("HomeView onAvatarUpload() /UploadAvatar", data);
          this.hintUser(data, "上传头像");
          if (data.code === 200) {
            this.getUserMessage().then(
              () => (this.avatarURL = this.generateAvatarURL())
            );
            this.uploadAvatar.visible = false;
          }
        })
        .catch((err) => {
          console.error("HomeView onAvatarUpload() /UploadAvatar", err);
        });
    },

    /**
     * 预测图片
     *
     * @param {string} path - 文件路径
     */
    predictImage(path) {
      const query = new URLSearchParams();
      query.append("predict_png_name", this.filename(path));

      this.$http.get("/PredictPng" + "?" + query).then(({ data }) => {
        console.log("predictImage", data);
        this.hintUser(data, "预测图片");
        if (data.code === 200) {
          this.getUploadedFiles();
        }
      });
    },

    /**
     * 删除图片
     *
     * @param {string} path - 文件路径
     */
    deleteImage(path) {
      const formData = new FormData();
      formData.append("delete_png_name", this.filename(path));

      this.$http.post("/DeletePng", formData).then(({ data }) => {
        console.log("deleteImage", data);
        this.hintUser(data, "删除图片");
        if (data.code === 200) {
          this.getUploadedFiles();
        }
      });
    },

    /**
     * 上传图片（限定 PNG）
     */
    uploadImage() {
      this.importFile(
        (file) => {
          const formData = new FormData();
          formData.append("png_name", file);
          this.$http.post("/UploadPng", formData).then(({ data }) => {
            console.log("UploadPng", data);
            this.hintUser(data, "上传图片");
            if (data.code === 200) {
              this.getUploadedFiles();
            }
          });
        },
        [".png"]
      );
    },

    /**
     * verify before upload avatar
     *
     * @param {File} file - file to be upload
     * @returns {boolean} wether to upload this file
     */
    beforeAvatarUpload(file) {
      const isImage = /^image/.test(file.type);
      const isLessThan2M = file.size / 1024 / 1024 < 2;
      if (!isImage) {
        this.$message.error({
          showClose: true,
          message: "请上传头像图片",
        });
      } else if (!isLessThan2M) {
        this.$message.error({
          showClose: true,
          message: "上传头像图片大小不能超过 2MB",
        });
      }
      return isImage && isLessThan2M;
    },

    clearUpdatePasswordForm() {
      this.setToEmpty(this.updatePasswordForm);
    },

    /**
     * 使用 updatePasswordForm 中的数据更新当前用户密码
     */
    async updatePassword() {
      try {
        await this.$refs.updatePasswordForm.validate();
        console.log("HomeView.vue updatePassword() passed validation");
      } catch (error) {
        console.log("HomeView.vue updatePassword() failed to pass validation");
        return;
      }

      this.updatePasswordForm.loading = true;

      const formData = new FormData();
      formData.append("oldPassword", this.updatePasswordForm.oldPassword);
      formData.append("newPassword", this.updatePasswordForm.newPassword);

      this.$http
        .post("/ModifyPassword", formData)
        .then(({ data }) => {
          console.log("HomeView.vue updatePassword /ModifyPassword", data);
          this.hintUser(data, "修改密码");
          if (data.code == 200) {
            this.updatePasswordForm.dialogVisible = false;
            this.clearUpdatePasswordForm();
          }
        })
        .catch((err) =>
          console.error("HomeView.vue updatePassword /ModifyPassword", err)
        )
        .finally(() => (this.updatePasswordForm.loading = false));
    },

    /**
     * 获取用户上传文件路径
     */
    getUploadedFiles() {
      this.$http
        .get("/GetUserFilesPath")
        .then((res) => {
          /**
           * @type {{code: number, msg: string, data: string[]}}
           */
          const data = res.data;
          console.log("HomeView getUploadedFiles() /GetUserFilesPath", data);
          this.hintUserWhenError(data, "获取上传文件");
          this.uploadedFiles = this.$R.map(this.$R.objOf("path"))(data.data);
        })
        .catch((err) => console.error("HomeView.vue getUploadedFiles()", err));
    },
  },

  mounted() {
    // 同步获取用户信息
    this.getUserMessage().then(
      () => (this.avatarURL = this.generateAvatarURL())
    );
    this.getUploadedFiles();
  },
};
</script>

<style lang="scss" scoped>
.home {
  display: flex;
  flex-flow: column nowrap;
  justify-content: space-around;
  align-items: center;

  $content-width: 50%;
  $content-min-width: 700px;

  .table {
    margin-top: 1em;
    width: $content-width;
    min-width: $content-min-width;
  }

  .card {
    width: $content-width;
    min-width: $content-min-width;
    $avatar-width: 150px;
    $avatar-height: 150px;
    .avatar {
      width: $avatar-width;
      height: $avatar-height;
    }
    .file-uploader {
      display: inline;
      margin-left: 0.7em;
    }
    .avatar-uploader {
      border: 1px dashed #d9d9d9;
      border-radius: 6px;
      cursor: pointer;
      overflow: hidden;
      width: $avatar-width;
      height: $avatar-height;
      &:hover {
        border-color: #409eff;
      }
      .avatar-uploader-icon {
        // 这里的 width 和 height 是为了保证按钮的大小
        width: $avatar-width;
        height: $avatar-height;
        color: #8c939d;
        line-height: $avatar-width;
        text-align: center;
      }
    }
  }
}
</style>
