import Vue from 'vue'
import App from './App.vue'
import './plugins/element.js'
import router from './router'
import store from './store'
import VueRamda from 'vue-ramda'
import * as R from 'ramda'

Vue.use(VueRamda)

import axios from "axios" // async request library
import ElementUI from 'element-ui'

Vue.config.productionTip = false
Vue.prototype.$http = axios // register globally as `this.$http`
axios.defaults.withCredentials = true; // carry Cookie, Auth header and TLS credentials when CORS
axios.defaults.baseURL = 'http://localhost:8080' // API Server

/** 
 * Global Function 
 */

/**
 * 根据返回的 response 使用 Message 提醒用户
 * @param {object} res - response
 * @param {number} res.code - response code
 * @param {string} res.msg - response message for user
 * @param {string=""} prefix - prefix add to res.msg
 */
function hintUser(res, prefix = "") {
  ElementUI.Message({
    showClose: true,

    message: R.cond([
      [R.isEmpty, R.always(res.msg)],
      [R.T, prefix => prefix + "：" + res.msg]
    ])(prefix),

    type: R.cond([
      [R.equals(200), R.always('success')],
      [R.T, R.always('error')]
    ])(res.code)
  })
}

/**
 * 当出错时使用 Message 提醒用户
 * @param {object} res - response
 * @param {number} res.code - response code
 * @param {string} res.msg - response message for user
 * @param {string=""} prefix - prefix add to res.msg
 */
function hintUserWhenError(res, prefix = "") {
  R.when(
    R.compose(R.not, R.propEq('code', 200)),
    () => hintUser(res, prefix)
  )(res);
}

/**
 * 获取用户信息，并在失败时回退到预登陆状态
 */
async function getUserMessage() {
  try {
    const res = await axios.get('/GetUserMessage')
    /**
     * @type {{code: number, msg: string, data: object}}
     */
    const data = res.data
    console.log("main getUserMessage()", data)
    hintUserWhenError(data, "获取用户信息")

    if (data.code === 200) {
      store.commit('login', data.data)
      if (router.currentRoute.name != 'HomeView') {
        router.push({
          name: "HomeView"
        })
      }
    } else {
      store.commit('logout')
      if (router.currentRoute.name != "LoginView") {
        router.push({
          name: "LoginView"
        })
      }
    }
  } catch (err) {
    console.error("main getUserMessage()", err)
    store.commit('logout')
    if (router.currentRoute.name != "LoginView") {
      router.push({
        name: "LoginView"
      })
    }
  }
}

/**
 * 被选中的文件
 * @callback selectedFile
 * @param {File} file - 被选择的文件
 * @returns {void}
 */

/**
 * 打开选择文件 Dialog 并返回文件或 undefined
 * @param {selectedFile} callback - 处理被选择的文件的 callback
 * @param {string[]} acceptList - 可以接受的文件后缀名 
 */
function importFile(callback, acceptList = []) {
  const input = document.createElement('input')
  input.type = 'file'
  input.multiple = false
  input.accept = R.ifElse(R.isEmpty, R.always('*'), R.join(','))(acceptList)
  input.oninput = () => {
    callback(R.head(input.files))
  }
  input.click()
}

Vue.mixin({
  methods: {
    hintUser: hintUser,
    hintUserWhenError: hintUserWhenError,
    getUserMessage: getUserMessage,
    importFile: importFile,

    /**
     * 将对象的 key 置为对应的空值，浅赋值
     * @param {object} obj 
     */
    setToEmpty(obj) {
      // 为了响应式就不能复制对象了
      for (let k of Object.keys(obj)) {
        obj[k] = R.empty(obj[k])
      }
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')