<template>
  <el-card class="login-card">
    <el-tabs v-model="activePanel" stretch @tab-click="clearForm">
      <el-tab-pane label="登陆" name="login">
        <el-form
          class="login-form"
          :model="form"
          :rules="form.loginRules"
          ref="loginForm"
          @submit.native.prevent="login"
        >
          <el-form-item prop="account">
            <template slot="label">
              <strong>账号</strong>
            </template>
            <el-input
              v-model="form.account"
              placeholder="请输入账号"
              prefix-icon="el-icon-user"
              clearable
            />
          </el-form-item>
          <el-form-item prop="password">
            <template slot="label">
              <strong>密码</strong>
            </template>
            <el-input
              v-model="form.password"
              placeholder="请输入密码"
              type="password"
              prefix-icon="el-icon-lock"
              show-password
              clearable
            />
          </el-form-item>
          <el-form-item>
            <el-button
              :loading="form.loading"
              class="login-button"
              type="primary"
              native-type="submit"
              block
              >登陆</el-button
            >
          </el-form-item>
          <el-link type="info" class="forgot-password">忘记密码？</el-link>
        </el-form>
      </el-tab-pane>
      <el-tab-pane label="注册" name="register">
        <el-form
          class="login-form"
          :model="form"
          :rules="form.registerRules"
          ref="registerForm"
        >
          <el-form-item prop="account">
            <template slot="label">
              <strong>账号</strong>
            </template>
            <el-input
              v-model="form.account"
              placeholder="请输入账号"
              prefix-icon="el-icon-user"
              clearable
            />
          </el-form-item>
          <el-form-item prop="email">
            <template slot="label">
              <strong>邮箱地址</strong>
            </template>
            <el-input
              v-model="form.email"
              placeholder="请输入邮箱"
              prefix-icon="el-icon-user"
              clearable
            />
          </el-form-item>

          <el-form-item prop="emailCode">
            <el-row type="flex" justify="space-between" :gutter="10">
              <el-col :span="16">
                <el-input
                  v-model="form.emailCode"
                  placeholder="请输入验证码"
                  :disabled="!form.emailValid"
                />
              </el-col>
              <el-col :span="8">
                <el-button
                  style="width: 100%"
                  type="primary"
                  :disabled="!form.emailValid"
                  @click="sendAuthCode"
                >
                  发送验证码
                </el-button>
              </el-col>
            </el-row>
          </el-form-item>
          <el-form-item prop="nickname">
            <template slot="label">
              <strong>昵称（可选）</strong>
            </template>
            <el-input
              v-model="form.nickname"
              placeholder="请输入昵称"
              prefix-icon="el-icon-user"
              clearable
            />
          </el-form-item>
          <el-form-item prop="password">
            <template slot="label">
              <strong>密码</strong>
            </template>
            <el-input
              v-model="form.password"
              placeholder="请输入密码"
              type="password"
              prefix-icon="el-icon-lock"
              show-password
              clearable
            />
          </el-form-item>
          <el-form-item prop="confirmPassword">
            <template slot="label">
              <strong>确认密码</strong>
            </template>
            <el-input
              v-model="form.confirmPassword"
              placeholder="请确认密码"
              type="password"
              prefix-icon="el-icon-lock"
              show-password
              clearable
            />
          </el-form-item>
          <el-form-item>
            <el-button
              :loading="form.loading"
              class="login-button"
              type="primary"
              @click="register"
              block
            >
              注册
            </el-button>
          </el-form-item>
          <el-link type="info" class="forgot-password">忘记密码？</el-link>
        </el-form>
      </el-tab-pane>
    </el-tabs>
  </el-card>
</template>

<script>
export default {
  data() {
    const basicRules = {
      account: [{ required: true, message: "请输入账号", trigger: "blur" }],
      password: [{ required: true, message: "请输入密码", trigger: "blur" }],
    };
    return {
      /** @type {'login'|'register'} */
      activePanel: "login",
      form: {
        account: "",
        email: "",
        emailValid: false,
        emailCode: "",
        nickname: "",
        password: "",
        confirmPassword: "",
        loading: false,
        loginRules: basicRules,
        registerRules: {
          ...basicRules,
          email: [
            {
              validator: async (_rule, value, callback) => {
                this.form.emailValid = false;
                this.$R.cond([
                  [this.$R.isEmpty, () => callback(new Error("请输入邮箱"))],
                  [
                    this.$R.complement(this.validateEmail),
                    () => callback(new Error("错误的邮箱格式")),
                  ],
                ])(value);
                let { passed, msg } = await this.checkEmailAvailable(value);
                if (!passed) callback(new Error(msg));
                this.form.emailValid = passed;
                callback();
              },
              trigger: "blur",
            },
          ],
          emailCode: [
            {
              required: true,
              message: "请输入验证码",
              trigger: "blur",
            },
          ],
          confirmPassword: [
            {
              required: true,
              message: "请确认密码",
              trigger: "blur",
            },
            {
              validator: (_rule, val, callback) => {
                if (this.form.password === val) callback();
                else callback(new Error("两次密码不一致"));
              },
            },
          ],
        },
      },
    };
  },
  methods: {
    clearForm() {
      this.$R.pipe(
        this.$R.keys,
        this.$R.filter((k) => this.$R.is(String, this.form[k])),
        this.$R.map((k) => (this.form[k] = this.$R.empty(this.form[k])))
      )(this.form);
    },

    /**
     * validate the E-mail in RFC 5322
     *
     * @param {string} email - E-mail address to be validated
     * @returns {boolean} if email meet RFC 5322 standard
     */
    validateEmail(email) {
      return this.$R.compose(
        this.$R.complement(this.$R.isEmpty),
        // 正则匹配字符串。
        // 注意，如果没有匹配项，则返回空数组。
        // 和 String.prototype.match 不同，后者在没有匹配项时会返回 null。
        this.$R.match(
          /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
        ),
        this.$R.toLower,
        // R.toString 对于不是幂等的
        // R.toString('abc') => '"abc"'
        String
      )(email);
    },

    async login() {
      // 执行表单验证
      let passed;
      this.$refs.loginForm.validate((res) => (passed = res));
      if (!passed) return;

      // prepare payload
      this.form.loading = true;
      const params = new URLSearchParams();
      params.append("account", this.form.account);
      params.append("password", this.form.password);

      // 给我同步
      await this.$http
        .get("/UserLogin?" + params)
        .then((res) => {
          const /** @type {{code: number, msg: string}} */ data = res.data;
          console.log("LoginForm login() /UserLogin?" + params, data);
          this.hintUser(data, "用户登录");
          if (data.code === 200) this.getUserMessage();
        })
        .catch((err) => {
          console.error("LoginForm login() /UserLogin?" + params, err);
        });

      this.form.loading = false;
    },

    /**
     * Check E-mail Available
     *
     * @param {string} email
     * @returns {Promise.<{passed: boolean, msg: string}>}
     */
    async checkEmailAvailable(email) {
      const params = new URLSearchParams();
      params.append("email", email);

      return this.$http
        .get("/CheckEmailAvailable?" + params)
        .then(({ data }) => {
          return {
            passed: data.code === 200,
            msg: data.msg,
          };
        })
        .catch(() => {
          return {
            passed: false,
            msg: "服务器错误",
          };
        });
    },

    sendAuthCode() {
      const params = new URLSearchParams();
      params.append("email", this.form.email);
      this.$http
        .get("/SendAuthCode?" + params)
        .then(({ data }) => {
          this.hintUser(data, "发送验证码");
        })
        .catch((err) =>
          console.error("LoginForm sendAuthCode() /SendAuthCode", err)
        );
    },

    async register() {
      // 执行表单验证
      let passed = await this.$refs.registerForm
        .validate()
        .then((res) => res)
        .catch((res) => res);
      if (!passed) return;

      // prepare payload
      this.form.loading = true;
      const params = new URLSearchParams();
      params.append("account", this.form.account);
      params.append("password", this.form.password);
      params.append("email", this.form.email);
      params.append("auth_code", this.form.emailCode);

      // 如果没有提供 nickname 就使用 account
      params.append(
        "nick_name",
        this.$R.when(
          this.$R.isEmpty,
          this.$R.always(this.form.account)
        )(this.form.nickname)
      );

      await this.$http
        .get("/UserRegister?" + params)
        .then(({ data }) => {
          console.log("LoginForm register() /UserRegister?" + params, data);
          this.hintUser(data);
          if (data.code == 200) {
            this.activePanel = "login";
            this.form.password = "";
          }
        })
        .catch((err) => {
          console.error("LoginForm register() /UserRegister?" + params, err);
        });

      this.form.loading = false;
    },
  },
};
</script>

<style lang="scss" scoped>
.login-card {
  display: flex;
  justify-content: center;
  align-items: center;
  .login-form {
    width: 350px;
    .login-button {
      width: 100%;
      margin-top: 20px;
    }
  }
}
</style>